@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background{background-size:cover;background-repeat:no-repeat;background-position:center center;overflow:hidden;will-change:transform;-webkit-backface-visibility:hidden;backface-visibility:hidden;height:120vh;position:fixed;width:100%;-webkit-transform:translateY(20vh);transform:translateY(20vh);transition:all 1.3s cubic-bezier(0.22, 0.44, 0, 1)}.background:before{content:'';position:absolute;width:100%;height:100%;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,0.3)}.background:first-child{background-image:url(/full-page-scroll/static/media/1.11ea1891.jpg);-webkit-transform:translateY(-10vh);transform:translateY(-10vh)}.background:first-child .content-wrapper{-webkit-transform:translateY(10vh);transform:translateY(10vh)}.background:nth-child(2){background-image:url(/full-page-scroll/static/media/2.48494d66.jpg)}.background:nth-child(3){background-image:url(/full-page-scroll/static/media/3.53bde113.jpg)}.background:nth-child(1){z-index:3}.background:nth-child(2){z-index:2}.background:nth-child(3){z-index:1}.content-wrapper{height:100vh;display:flex;justify-content:center;text-align:center;flex-flow:column nowrap;color:#fff;font-family:Montserrat;text-transform:uppercase;-webkit-transform:translateY(40vh);transform:translateY(40vh);will-change:transform;-webkit-backface-visibility:hidden;backface-visibility:hidden;transition:all 1.8s cubic-bezier(0.22, 0.44, 0, 1)}.content-title{font-size:12vh;line-height:1.4}.background.up-scroll{-webkit-transform:translate3d(0, -10vh, 0);transform:translate3d(0, -10vh, 0)}.background.up-scroll .content-wrapper{-webkit-transform:translateY(10vh);transform:translateY(10vh)}.background.up-scroll+.background{-webkit-transform:translate3d(0, 20vh, 0);transform:translate3d(0, 20vh, 0)}.background.up-scroll+.background .content-wrapper{-webkit-transform:translateY(20vh);transform:translateY(20vh)}.background.down-scroll{-webkit-transform:translate3d(0, -120vh, 0);transform:translate3d(0, -120vh, 0)}.background.down-scroll .content-wrapper{-webkit-transform:translateY(40vh);transform:translateY(40vh)}.background.down-scroll+.background:not(.down-scroll){-webkit-transform:translate3d(0, -10vh, 0);transform:translate3d(0, -10vh, 0)}.background.down-scroll+.background:not(.down-scroll) .content-wrapper{-webkit-transform:translateY(10vh);transform:translateY(10vh)}

